`, "",{"version":3,"sources":["webpack://./src/components/AreaChart.vue"],"names":[],"mappings":";AA2KA;CACA,kBAAA;CACA,WAAA;CACA,aAAA;AACA;AAEA;CACA,sBAAA;CACA,uBAAA;AACA","sourcesContent":["<template>\n\t<div class=\"area-chart\">\n\t\t<canvas ref=\"chartCanvas\" :width=\"width\" :height=\"height\"></canvas>\n\t</div>\n</template>\n\n<script>\nimport {\n\tChart,\n\tLineController,\n\tLineElement,\n\tPointElement,\n\tFiller,\n\tCategoryScale,\n\tLinearScale,\n\tTooltip,\n} from 'chart.js'\n\nChart.register(LineController, LineElement, PointElement, Filler, CategoryScale, LinearScale, Tooltip)\n\nexport default {\n\tname: 'AreaChart',\n\tprops: {\n\t\tlabels: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\tdata: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\twidth: {\n\t\t\ttype: Number,\n\t\t\tdefault: 400,\n\t\t},\n\t\theight: {\n\t\t\ttype: Number,\n\t\t\tdefault: 220,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tchart: null,\n\t\t}\n\t},\n\tmounted() {\n\t\tthis.renderChart()\n\t},\n\tbeforeDestroy() {\n\t\tif (this.chart) {\n\t\t\tthis.chart.destroy()\n\t\t}\n\t},\n\twatch: {\n\t\tdata() {\n\t\t\tif (this.chart) {\n\t\t\t\tthis.chart.destroy()\n\t\t\t}\n\t\t\tthis.renderChart()\n\t\t},\n\t},\n\tmethods: {\n\t\trenderChart() {\n\t\t\tconst ctx = this.$refs.chartCanvas.getContext('2d')\n\n\t\t\t// Gradient fill\n\t\t\tconst gradient = ctx.createLinearGradient(0, 0, 0, this.height)\n\t\t\tgradient.addColorStop(0, 'rgba(200, 120, 200, 0.35)')\n\t\t\tgradient.addColorStop(1, 'rgba(200, 120, 200, 0.02)')\n\n\t\t\tthis.chart = new Chart(ctx, {\n\t\t\t\ttype: 'line',\n\t\t\t\tdata: {\n\t\t\t\t\tlabels: this.labels,\n\t\t\t\t\tdatasets: [\n\t\t\t\t\t\t{\n\t\t\t\t\t\t\tdata: this.data,\n\t\t\t\t\t\t\tborderColor: '#c878c8',\n\t\t\t\t\t\t\tbackgroundColor: gradient,\n\t\t\t\t\t\t\tborderWidth: 2.5,\n\t\t\t\t\t\t\tpointBackgroundColor: '#c878c8',\n\t\t\t\t\t\t\tpointBorderColor: '#ffffff',\n\t\t\t\t\t\t\tpointBorderWidth: 2,\n\t\t\t\t\t\t\tpointRadius: 4,\n\t\t\t\t\t\t\tpointHoverRadius: 6,\n\t\t\t\t\t\t\tfill: true,\n\t\t\t\t\t\t\ttension: 0.35,\n\t\t\t\t\t\t},\n\t\t\t\t\t],\n\t\t\t\t},\n\t\t\t\toptions: {\n\t\t\t\t\tresponsive: true,\n\t\t\t\t\tmaintainAspectRatio: false,\n\t\t\t\t\tplugins: {\n\t\t\t\t\t\tlegend: { display: false },\n\t\t\t\t\t\ttooltip: {\n\t\t\t\t\t\t\tbackgroundColor: '#1a1a2e',\n\t\t\t\t\t\t\ttitleFont: { size: 13, weight: '600' },\n\t\t\t\t\t\t\tbodyFont: { size: 12 },\n\t\t\t\t\t\t\tpadding: 10,\n\t\t\t\t\t\t\tcornerRadius: 8,\n\t\t\t\t\t\t\tcallbacks: {\n\t\t\t\t\t\t\t\tlabel: (context) => ` ${context.parsed.y} completed tasks`,\n\t\t\t\t\t\t\t},\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t\tscales: {\n\t\t\t\t\t\tx: {\n\t\t\t\t\t\t\tgrid: { display: false },\n\t\t\t\t\t\t\tticks: {\n\t\t\t\t\t\t\t\tfont: { size: 11 },\n\t\t\t\t\t\t\t\tcolor: '#9ca3af',\n\t\t\t\t\t\t\t},\n\t\t\t\t\t\t},\n\t\t\t\t\t\ty: {\n\t\t\t\t\t\t\tbeginAtZero: true,\n\t\t\t\t\t\t\tgrid: {\n\t\t\t\t\t\t\t\tcolor: 'rgba(0,0,0,0.04)',\n\t\t\t\t\t\t\t},\n\t\t\t\t\t\t\tticks: {\n\t\t\t\t\t\t\t\tfont: { size: 11 },\n\t\t\t\t\t\t\t\tcolor: '#9ca3af',\n\t\t\t\t\t\t\t\tstepSize: 2,\n\t\t\t\t\t\t\t},\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t\tlayout: {\n\t\t\t\t\t\tpadding: { top: 20 },\n\t\t\t\t\t},\n\t\t\t\t},\n\t\t\t\tplugins: [\n\t\t\t\t\t{\n\t\t\t\t\t\tid: 'peakLabel',\n\t\t\t\t\t\tafterDraw: (chart) => {\n\t\t\t\t\t\t\tconst dataset = chart.data.datasets[0]\n\t\t\t\t\t\t\tconst meta = chart.getDatasetMeta(0)\n\t\t\t\t\t\t\tconst maxVal = Math.max(...dataset.data)\n\t\t\t\t\t\t\tconst maxIdx = dataset.data.indexOf(maxVal)\n\t\t\t\t\t\t\tconst point = meta.data[maxIdx]\n\n\t\t\t\t\t\t\tif (point) {\n\t\t\t\t\t\t\t\tconst { ctx: c } = chart\n\t\t\t\t\t\t\t\tc.save()\n\t\t\t\t\t\t\t\tconst text = `${maxVal} completed tasks`\n\t\t\t\t\t\t\t\tc.font = '11px -apple-system, BlinkMacSystemFont, sans-serif'\n\t\t\t\t\t\t\t\tconst textWidth = c.measureText(text).width\n\t\t\t\t\t\t\t\tconst boxPad = 6\n\t\t\t\t\t\t\t\tconst boxX = point.x - textWidth / 2 - boxPad\n\t\t\t\t\t\t\t\tconst boxY = point.y - 30\n\n\t\t\t\t\t\t\t\tc.fillStyle = 'rgba(26, 26, 46, 0.85)'\n\t\t\t\t\t\t\t\tc.beginPath()\n\t\t\t\t\t\t\t\tc.roundRect(boxX, boxY, textWidth + boxPad * 2, 18, 4)\n\t\t\t\t\t\t\t\tc.fill()\n\n\t\t\t\t\t\t\t\tc.fillStyle = '#ffffff'\n\t\t\t\t\t\t\t\tc.textAlign = 'center'\n\t\t\t\t\t\t\t\tc.textBaseline = 'middle'\n\t\t\t\t\t\t\t\tc.fillText(text, point.x, boxY + 9)\n\t\t\t\t\t\t\t\tc.restore()\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t],\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.area-chart {\n\tposition: relative;\n\twidth: 100%;\n\theight: 220px;\n}\n\n.area-chart canvas {\n\twidth: 100% !important;\n\theight: 100% !important;\n}\n</style>\n"],"sourceRoot":""}]);
