`, "",{"version":3,"sources":["webpack://./src/components/DonutChart.vue"],"names":[],"mappings":";AAiIA;CACA,aAAA;CACA,sBAAA;CACA,mBAAA;CACA,4BAAA;AACA;AAEA;CACA,gBAAA;CACA,iBAAA;AACA;AAEA;CACA,aAAA;CACA,mBAAA;CACA,uBAAA;CACA,4BAAA;CACA,eAAA;AACA;AAEA;CACA,aAAA;CACA,mBAAA;CACA,QAAA;AACA;AAEA;CACA,WAAA;CACA,YAAA;CACA,kBAAA;CACA,cAAA;AACA;AAEA;CACA,eAAA;CACA,2CAAA;CACA,gBAAA;AACA","sourcesContent":["<template>\n\t<div class=\"donut-chart\">\n\t\t<canvas ref=\"chartCanvas\" width=\"220\" height=\"220\"></canvas>\n\t\t<div class=\"donut-chart__legend\">\n\t\t\t<div\n\t\t\t\tv-for=\"(label, index) in chartData.labels\"\n\t\t\t\t:key=\"index\"\n\t\t\t\tclass=\"donut-chart__legend-item\"\n\t\t\t>\n\t\t\t\t<span\n\t\t\t\t\tclass=\"donut-chart__legend-dot\"\n\t\t\t\t\t:style=\"{ backgroundColor: chartData.colors[index] }\"\n\t\t\t\t></span>\n\t\t\t\t<span class=\"donut-chart__legend-label\">{{ label }}</span>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport {\n\tChart,\n\tDoughnutController,\n\tArcElement,\n\tTooltip,\n\tLegend,\n} from 'chart.js'\n\nChart.register(DoughnutController, ArcElement, Tooltip, Legend)\n\nexport default {\n\tname: 'DonutChart',\n\tprops: {\n\t\tchartData: {\n\t\t\ttype: Object,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tchart: null,\n\t\t}\n\t},\n\tmounted() {\n\t\tthis.renderChart()\n\t},\n\tbeforeDestroy() {\n\t\tif (this.chart) {\n\t\t\tthis.chart.destroy()\n\t\t}\n\t},\n\tmethods: {\n\t\trenderChart() {\n\t\t\tconst ctx = this.$refs.chartCanvas.getContext('2d')\n\t\t\tconst total = this.chartData.data.reduce((a, b) => a + b, 0)\n\n\t\t\tthis.chart = new Chart(ctx, {\n\t\t\t\ttype: 'doughnut',\n\t\t\t\tdata: {\n\t\t\t\t\tlabels: this.chartData.labels,\n\t\t\t\t\tdatasets: [\n\t\t\t\t\t\t{\n\t\t\t\t\t\t\tdata: this.chartData.data,\n\t\t\t\t\t\t\tbackgroundColor: this.chartData.colors,\n\t\t\t\t\t\t\tborderColor: '#ffffff',\n\t\t\t\t\t\t\tborderWidth: 3,\n\t\t\t\t\t\t\thoverBorderWidth: 3,\n\t\t\t\t\t\t\thoverOffset: 4,\n\t\t\t\t\t\t},\n\t\t\t\t\t],\n\t\t\t\t},\n\t\t\t\toptions: {\n\t\t\t\t\tresponsive: true,\n\t\t\t\t\tmaintainAspectRatio: true,\n\t\t\t\t\tcutout: '58%',\n\t\t\t\t\tplugins: {\n\t\t\t\t\t\tlegend: {\n\t\t\t\t\t\t\tdisplay: false,\n\t\t\t\t\t\t},\n\t\t\t\t\t\ttooltip: {\n\t\t\t\t\t\t\tbackgroundColor: '#1a1a2e',\n\t\t\t\t\t\t\ttitleFont: { size: 13, weight: '600' },\n\t\t\t\t\t\t\tbodyFont: { size: 12 },\n\t\t\t\t\t\t\tpadding: 10,\n\t\t\t\t\t\t\tcornerRadius: 8,\n\t\t\t\t\t\t\tcallbacks: {\n\t\t\t\t\t\t\t\tlabel: function(context) {\n\t\t\t\t\t\t\t\t\tconst value = context.parsed\n\t\t\t\t\t\t\t\t\tconst percentage = Math.round((value / total) * 100)\n\t\t\t\t\t\t\t\t\treturn ` ${context.label}: ${percentage}%`\n\t\t\t\t\t\t\t\t},\n\t\t\t\t\t\t\t},\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t\tlayout: {\n\t\t\t\t\t\tpadding: 10,\n\t\t\t\t\t},\n\t\t\t\t},\n\t\t\t\tplugins: [\n\t\t\t\t\t{\n\t\t\t\t\t\tid: 'percentageLabels',\n\t\t\t\t\t\tafterDraw: (chart) => {\n\t\t\t\t\t\t\tconst { ctx: context, data: chartDataRef } = chart\n\t\t\t\t\t\t\tchart.getDatasetMeta(0).data.forEach((arc, i) => {\n\t\t\t\t\t\t\t\tconst value = chartDataRef.datasets[0].data[i]\n\t\t\t\t\t\t\t\tconst percentage = Math.round((value / total) * 100)\n\t\t\t\t\t\t\t\tconst midAngle = (arc.startAngle + arc.endAngle) / 2\n\t\t\t\t\t\t\t\tconst radius = (arc.outerRadius + arc.innerRadius) / 2\n\t\t\t\t\t\t\t\tconst x = arc.x + Math.cos(midAngle) * radius\n\t\t\t\t\t\t\t\tconst y = arc.y + Math.sin(midAngle) * radius\n\n\t\t\t\t\t\t\t\tcontext.save()\n\t\t\t\t\t\t\t\tcontext.fillStyle = '#ffffff'\n\t\t\t\t\t\t\t\tcontext.font = 'bold 12px -apple-system, BlinkMacSystemFont, sans-serif'\n\t\t\t\t\t\t\t\tcontext.textAlign = 'center'\n\t\t\t\t\t\t\t\tcontext.textBaseline = 'middle'\n\t\t\t\t\t\t\t\tcontext.fillText(`${percentage}%`, x, y)\n\t\t\t\t\t\t\t\tcontext.restore()\n\t\t\t\t\t\t\t})\n\t\t\t\t\t\t},\n\t\t\t\t\t},\n\t\t\t\t],\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.donut-chart {\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tgap: var(--spacing-lg, 24px);\n}\n\n.donut-chart canvas {\n\tmax-width: 220px;\n\tmax-height: 220px;\n}\n\n.donut-chart__legend {\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tgap: var(--spacing-lg, 24px);\n\tflex-wrap: wrap;\n}\n\n.donut-chart__legend-item {\n\tdisplay: flex;\n\talign-items: center;\n\tgap: 6px;\n}\n\n.donut-chart__legend-dot {\n\twidth: 10px;\n\theight: 10px;\n\tborder-radius: 50%;\n\tflex-shrink: 0;\n}\n\n.donut-chart__legend-label {\n\tfont-size: 13px;\n\tcolor: var(--color-text-secondary, #6b7280);\n\tfont-weight: 400;\n}\n</style>\n"],"sourceRoot":""}]);
