.perf-panel[data-v-15a8b195] {
	margin-bottom: var(--spacing-xl, 32px);
}

/*  Header  */
.perf-panel__header[data-v-15a8b195] {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
