	margin: 0;
	padding: 0;
	border: none;
}
.perf-panel__collapse-btn[data-v-15a8b195] {
	background: var(--bg-card, #fff);
	border: 1px solid var(--color-border, #e5e7eb);
	border-radius: 50%;
