	height: 36px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	color: var(--color-text-secondary, #6b7280);
	transition: all 0.2s ease;
	flex-shrink: 0;
	margin-top: 4px;
}
.perf-panel__collapse-btn[data-v-15a8b195]:hover {
	background-color: var(--bg-page, #f5f6fa);
}
.perf-panel__collapse-icon[data-v-15a8b195] {
	transition: transform 0.3s ease;
}
.perf-panel__collapse-icon--rotated[data-v-15a8b195] {
	transform: rotate(180deg);
}

/*  Grids  */
.perf-panel__top-grid[data-v-15a8b195] {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: var(--spacing-md, 16px);
	margin-bottom: var(--spacing-md, 16px);
}
.perf-panel__bottom-grid[data-v-15a8b195] {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: var(--spacing-md, 16px);
}

/*  Card  */
.perf-panel__card[data-v-15a8b195] {
	background: var(--bg-card, #fff);
	border-radius: var(--radius-card, 12px);
