.perf-panel__card-title-underline[data-v-15a8b195] {
	width: 36px;
	height: 3px;
	background-color: #c878c8;
	border-radius: 2px;
	margin-bottom: var(--spacing-lg, 24px);
}
.perf-panel__card-desc[data-v-15a8b195] {
	font-size: 13px;
	color: var(--color-text-muted, #9ca3af);
	margin: 0 0 var(--spacing-lg, 24px) 0;
	line-height: 1.5;
}

/*  Progress Bars  */
.perf-panel__bar-list[data-v-15a8b195] {
	display: flex;
	flex-direction: column;
	gap: var(--spacing-md, 16px);
}
.perf-panel__bar-item[data-v-15a8b195] {
	display: flex;
	flex-direction: column;
	gap: 6px;
}
.perf-panel__bar-row[data-v-15a8b195] {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.perf-panel__bar-label[data-v-15a8b195] {
	font-size: 13px;
	color: var(--color-text-primary, #1a1a2e);
	font-weight: 400;
}
.perf-panel__bar-value[data-v-15a8b195] {
	font-size: 13px;
	font-weight: 600;
	color: var(--color-text-primary, #1a1a2e);
}
.perf-panel__bar-track[data-v-15a8b195] {
	width: 100%;
	height: 6px;
	background-color: #f0f1f5;
	border-radius: 3px;
	overflow: hidden;
}
.perf-panel__bar-fill[data-v-15a8b195] {
	height: 100%;
	background: linear-gradient(90deg, #c878c8, #d494d4);
	border-radius: 3px;
