/*  Project Navigator  */
.perf-panel__navigator[data-v-15a8b195] {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: var(--spacing-md, 16px);
	margin-bottom: var(--spacing-lg, 24px);
}
.perf-panel__nav-btn[data-v-15a8b195] {
	background: none;
	border: none;
	padding: 0;
	cursor: pointer;
	font-size: 22px;
	line-height: 1;
