	transition: color 0.2s ease;
	flex-shrink: 0;
}
.perf-panel__nav-btn[data-v-15a8b195]:hover {
	color: var(--color-text-primary, #1a1a2e);
}
.perf-panel__nav-label[data-v-15a8b195] {
	font-size: 13px;
	font-weight: 500;
	color: var(--color-text-primary, #1a1a2e);
	text-align: center;
	flex: 1;
	min-width: 0;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

/*  Responsive  */
@media (max-width: 1024px) {
.perf-panel__top-grid[data-v-15a8b195],
	.perf-panel__bottom-grid[data-v-15a8b195] {
		grid-template-columns: 1fr;
}
